/* --- CUSTOM CSS --- */
/** Media Queries **/
/*utilizar en H1, H2, H3*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  color: #1a1b15;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

/*
 *SELECTOR DE ATRIBUTOS
 * $ elementos cuyo atributo termina con...
 * ^ elementos cuyo atributo comienza con...
 * * elementos cuyo atributo contenga...
 * ~ elementos cuyo atributo class contenga...
*/
[class$=__heading] {
  font-family: "Outfit", sans-serif;
  color: #1a1b15;
  margin: 2rem 0;
  text-align: center;
  margin: 6rem 0 3rem 0;
}

.auth {
  /*600 px*/
  width: min(95%, 60rem);
  margin: 0 auto;
}
.auth__texto {
  text-align: center;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
}
.formulario__bloque {
  display: flex;
  flex-direction: column;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  width: 4rem;
  background-color: #1E293B;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label, .formulario__texto {
  color: #515442;
  margin-bottom: 0.5rem;
}
.formulario__input, .formulario__input--sociales {
  padding: 1rem;
  border: 1px solid #515442;
  border-radius: 0.5rem;
}
.formulario__input--sociales {
  flex: 1;
}
.formulario__imagen-actual {
  max-width: 20rem;
  margin-bottom: 2rem;
}
.formulario__input[type=file], [type=file].formulario__input--sociales {
  border: none;
}
.formulario__listado {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.formulario__contenedor-tag {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 0.7rem;
  align-items: center;
  justify-content: center;
  background-color: #007df4;
  border-radius: 0.5rem;
  padding: 1rem;
  width: auto;
}
.formulario__contenedor-tag > li {
  color: #FFFFFF;
  font-size: 1.6rem;
}
.formulario__contenedor-tag > i {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.formulario__contenedor-tag > i:hover {
  cursor: pointer;
  color: #ff4343;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  align-self: flex-start;
  width: auto;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar {
  width: 90%;
  margin: 0 auto;
}

.acciones, .acciones--centrar {
  margin: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #1a1b15;
  font-weight: 400;
  font-size: 1.6rem;
}
.acciones__enlace:hover {
  color: #003c75;
}

.header {
  height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.6)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, cover, cover;
  background-position: top, bottom, center center, center center;
}
@media (min-width: 768px) {
  .header {
    background-position: top left, bottom right, center center, center center;
  }
}
.header__contenedor {
  /*1200 px*/
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlace {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
}
.header__enlace:hover {
  color: #007df4;
  cursor: pointer;
}
.header__contenido {
  padding-top: 10rem;
  height: 100%;
}
@media (min-width: 768px) {
  .header__contenido {
    padding-top: 6rem;
  }
}
.header__logo {
  font-size: clamp(3.4rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    display: inline-block;
    text-align: left;
  }
}
.header__texto {
  font-size: 3rem;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
@media (min-width: 768px) {
  .header__texto {
    text-align: left;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  margin: 2rem 0;
  text-transform: uppercase;
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}

.barra {
  background-color: #007df4;
}
.barra__contenido {
  /*1200 px*/
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 768px) {
  .barra__logo {
    margin: 0;
    text-align: left;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2rem;
  text-transform: uppercase;
  padding: 2rem;
  display: block;
  text-align: center;
}
.navegacion__enlace:hover {
  background-color: #FFFFFF;
  color: #007df4;
}

.footer {
  background-color: #007df4;
  padding-top: 3rem;
  margin-top: 5rem;
  color: #FFFFFF;
}
.footer__grid {
  /*1200 px*/
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
    text-align: left;
  }
}
.footer__texto {
  text-align: center;
  line-height: 2;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
    padding: 3rem 0 0 0;
  }
}
.footer__copyright {
  background-color: #0063c1;
  margin: 3rem 0 0 0;
  padding: 2rem 0;
  color: #FFFFFF;
  text-align: center;
  font-weight: 900;
  font-size: 1.4rem;
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  padding-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
  align-items: center;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smooting: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="https://facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="https://github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="https://tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="https://instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="https://youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="https://twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-redes {
    justify-content: flex-end;
  }
}

.alerta {
  margin-bottom: 1rem;
  padding: 1rem 1rem 1rem 3rem;
}
.alerta__error {
  background-color: #ffc3c3;
  border-left: 5px solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta__exito {
  background-color: #abfeab;
  border-left: 5px solid #02a802;
  color: #02a802;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
  padding: 2rem;
}
@media (min-width: 768px) {
  .dashboard__header {
    flex-direction: row;
  }
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 768px) {
  .dashboard__logo {
    margin: 0;
    text-align: left;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__form--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
}
.dashboard__form--logout:hover {
  color: #007df4;
  cursor: pointer;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
}
.dashboard__menu {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}
.dashboard__enlace {
  color: #91a5c7;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border-radius: 0.7rem;
  transition: 300ms background-color ease-in-out, 300ms color ease;
}
.dashboard__enlace:hover, .dashboard__enlace--activo {
  background-color: #40587f;
  color: #FFFFFF;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: bold;
  margin-top: 1rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 1rem 3rem 1rem;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
.dashboard__titulo {
  text-align: center;
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
  }
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.2rem;
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  background-color: #FFFFFF;
  padding: 2rem;
  margin-top: 5rem;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
  margin-top: 5rem;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__head {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: #edf2f7;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
.table__acciones {
  all: unset;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.table__acciones--eliminar {
  color: rgb(169, 0, 0);
}
.table__acciones--editar {
  color: #007df4;
}
.table__acciones:hover {
  cursor: pointer;
}